<template>
    <div id="app" class="container">
        <div class="card" v-if="post">
            <div class="card-header">Vue Fetch POST</div>
            <div class="card-body" v-for="expense in post" :key="expense.id">
                <div class="form-group">
                    <input type="text" class="form-control" ref="post_id" :value=expense.id disabled />
                </div>
                <div class="form-group">
                    <input type="text" class="form-control" ref="post_name" :value=expense.name />
                </div>
                <div class="form-group">
                    <input type="text" class="form-control" ref="post_amount" :value=expense.amount />
                </div>
                <div class="form-group">
                    <input type="text" class="form-control" ref="post_transactionDate" :value=expense.transactionDate />
                </div>
                <button class="btn btn-sm btn-primary" @click="postData">Post Data</button>
                <button class="btn btn-sm btn-warning ml-2" @click="clearPostOutput">Clear</button>

                <div v-if="postResult" class="alert alert-secondary mt-2" role="alert"><pre>{{postResult}}</pre></div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "EditView",
        props: ['id'],
        data() {
            return {
                loading: false,
                post: null,
                postResult: null
            }
        },
        created() {
            this.fetchData();
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData() {
                this.post = null;
                this.loading = true;

                fetch('https://localhost:7007/expenses/')
                    .then(r => r.json())
                    .then(json => {
                        this.post = json;
                        this.loading = false;
                        return
                    });
            },

            async postData() {
                const postData = {
                    name: this.$refs.post_name.value,
                    amount: this.$refs.post_amount.value,
                    transactionDate: this.$refs.post_transactionDate.value,
                };

                try {
                    await fetch(`https://localhost:7007/expenses/`, {
                        method: "post",
                        headers: {
                            "Content-Type": "application/json"
                        },
                        body: JSON.stringify(postData),
                    }).then(function (response) {
                        console.log("ez a response " + response.json(postData));
                    }).then(function (text) {
                        console.log("ez a text " +text);
                    }).catch(function (error) {
                        console.error(error);
                    });
                } catch (err) {
                    alert(err); // Failed to fetch
                }
            },

            clearPostOutput() {
                this.postResult = null;
            },
        }
    }
</script>